import { Component, OnInit } from '@angular/core';
import { OpenFoodFactsService } from '../services/open-food-facts.service'
import { Router } from '@angular/router';

@Component({
  selector: 'app-ingredients',
  templateUrl: './ingredients.component.html',
  styleUrls: ['./ingredients.component.css']
})
export class IngredientsComponent implements OnInit {
  name : string;
  products = null;
  showDetails = false;

  currentPage;
  pageSize;
  totalResults;

  productCode : string;
  productName : string;
  productQuantity : string;
  productBrands : string;
  productImageUrl : string;
  productProteins100g : string;
  productCarbohydrates100g : string;
  productFat100g : string;
  productEnergyValue : string;
  productUrl : string;

  constructor(private openFoodFactsService: OpenFoodFactsService, private router: Router) { }

  ngOnInit() {
  }

  onSearchItem(){
  	console.log("Searching Item : " + this.name);
    this.openFoodFactsService.searchItems(this.name, 1, (data)=>{
      console.log(data);
      this.products = data.products;
      this.pageSize = data.page_size;
      this.currentPage = data.page;
      this.totalResults = data.count;
    })
  }

  onAddFavorite(value){
    console.log("Adding Item to Favorites : " + value);
    
  }

  onHideDetails() {
    this.showDetails = false;
  }

  onDisplayDetails(product){
    console.log("Checking details of : " + product);
    this.productCode = product.code; 
    this.productName = product.product_name;
    this.productQuantity = product.quantity;
    this.productBrands = product.brands;
    this.productImageUrl = product.image_url;
    this.productProteins100g = product.nutriments.proteins_100g;
    this.productCarbohydrates100g = product.nutriments.carbohydrates_100g;
    this.productFat100g = product.nutriments.fat_100g;
    this.productEnergyValue = product.nutriments.energy_value;
    this.productUrl = product.url;
    this.showDetails = true;
  }

  onNewPage(event) {
    console.log("scanning page : " + event.pageIndex);
    this.openFoodFactsService.searchItems(this.name, event.pageIndex + 1, (data)=>{
      console.log(data);
      this.products = data.products;
      this.pageSize = data.page_size;
      this.currentPage = data.page;
      this.totalResults = data.count;
    })
  }

  onClearResults() {
    this.products = null;
  }
}
