import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute } from "@angular/router";
import { UserLoginService } from "../services/user-login.service";
import { AuthenticateCallback, IsLoggedInCallback } from "../services/cognito.service";
import {FormControl} from '@angular/forms';

@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  styleUrls: ['./signin.component.css']
})
export class SigninComponent implements OnInit {

  email: string;
  password: string;
  errorMessage: string;

  isNewPasswordRequired: boolean;

  newPassword: string;
  newPasswordConfirmation: string;

  isConfirmationRequired: boolean;

  redirectUri: string;

  constructor(public router: Router, public userService: UserLoginService, public route: ActivatedRoute) {
    console.log("LoginComponent constructor");
  }

  ngOnInit() {
    this.route
        .queryParams
        .subscribe(params => {
            this.redirectUri = params['redirect_uri'];
        });

    this.errorMessage = null;
    console.log("Checking if the user is already authenticated.");

    let myThis = this;

    this.userService.isAuthenticated({
      onSuccess() {
        console.log("User is already logged in: redirecting !");
        if(this.redirectUri != null){
          myThis.router.navigate([myThis.redirectUri]);
        }
        else
          myThis.router.navigate(['/home']);
      },
      onFailure(message) {
        console.log('User is not logged in: ' + message);
      }
    });
  }

  onLogin() {
    let myThis = this;

    if (this.email == null || this.password == null) {
      this.errorMessage = "All fields are required";
      return;
    }
    this.errorMessage = null;
    this.userService.authenticate(this.email, this.password, {
      onSuccess(userConfirmationNecessary) {
        console.log("Authentication success: redirecting. Confirm = "  + userConfirmationNecessary);
        if(this.redirectUri != null){
          myThis.router.navigate([myThis.redirectUri]);
        }
        else
          myThis.router.navigate(['/home']);
      },
      onFailure(message) {
        console.log(message);
        myThis.errorMessage = message;
      },
      newPasswordRequired(userAttributes, requiredAttributes) {
        console.log(userAttributes);
        console.log(requiredAttributes);
        var newPassword = prompt('Enter new password ' ,'');
        myThis.userService.confirmNewPassword(myThis.email, newPassword, {
          onSuccess() {
            console.log("Change Password Success");
          },
          onFailure(message) {
            console.log("Can't change password: " + message);
          }
        });
      }
    })
  };
  /*
  isLoggedIn(message: string, isLoggedIn: boolean) {
    if (isLoggedIn) {
      this.router.navigate(['/news']);
    }
  }*/
}
