import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormControl } from '@angular/forms';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';
import { Router } from '@angular/router';
import { AuthService } from "./services/auth.service";
import { UserLoginService } from "./services/user-login.service";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  shouldRun = true;
  opened = false;
  authStatusSub: Subscription;

  isAuthenticated: boolean;

  constructor(public authService: AuthService, public userLoginService: UserLoginService, private router: Router) {
    console.log("AppComponent: constructor");
  }

  ngOnInit() {
    // We subscribe to a subject which allows us to get the current user's authentication status
    this.authStatusSub = this.authService.authStatusSubject.subscribe(
      authStatus => { 
        this.isAuthenticated = authStatus; 
      }
    );

    this.userLoginService.isAuthenticated({
      onSuccess(){
        console.log("User is authenticated");
      },
      onFailure(){
        console.log("User is not authenticated")
      }
    });
  }

  ngOnDestroy(){
    this.authStatusSub.unsubscribe();
  }

  onSignOut() {
    this.userLoginService.logout();
    this.router.navigate(['/signin']);
  }
}
