import { Injectable } from "@angular/core";
import { environment } from "../../environments/environment";
import { CognitoUserPool } from "amazon-cognito-identity-js";
import * as AWS from "aws-sdk/global";
import * as CognitoIdentity from "aws-sdk/clients/cognitoidentity";

export interface AuthenticateCallback {
    onSuccess: (userConfirmationNecessary?: boolean) => void,
    onFailure: (message: string) => void,
    newPasswordRequired?: (userAttributes: any, requiredAttributes: any) => void
}

export interface IsLoggedInCallback {
    onSuccess: () => void,
    onFailure: (message: string) => void
}

export interface SetNewPasswordCallback {
    onSuccess: () => void,
    onFailure: (message: string) => void
}

@Injectable()
export class CognitoUtil {

    public static _REGION = environment.region;
    public static _USER_POOL_ID = environment.userPoolId;
    public static _CLIENT_ID = environment.clientId;

    public static _POOL_DATA: any = {
        UserPoolId: CognitoUtil._USER_POOL_ID,
        ClientId: CognitoUtil._CLIENT_ID
    };

    public cognitoCreds: AWS.CognitoIdentityCredentials;

    getUserPool() {
        return new CognitoUserPool(CognitoUtil._POOL_DATA);
    }


    getCurrentUser() {
        return this.getUserPool().getCurrentUser();
    }

    getAccessToken(callback): void {
        if (callback == null) {
            throw("CognitoUtil: callback in getAccessToken is null...returning");
        }
        if (this.getCurrentUser() != null) {
            this.getCurrentUser().getSession(function (err, session) {
                if (err) {
                    console.log("CognitoUtil: Can't get session:" + err);
                    callback(null, "Can't get session");
                }
                else {
                    if (session.isValid()) {
                        callback(session.getAccessToken().getJwtToken(), null);
                    }
                    else {
                        console.log("CognitoUtil: Session is invalid");
                        callback(null, "Session is invalid")
                    }
                }
            });
        }
        else {
            console.log(" CognitoUtil: Can't get current user");
            callback(null, "Can't get current user");
        }
    }

    getIdToken(callback): void {
        if (callback == null) {
            throw("CognitoUtil: callback in getIdToken is null...returning");
        }
        if (this.getCurrentUser() != null)
            this.getCurrentUser().getSession(function (err, session) {
                if (err) {
                    console.log("CognitoUtil: Can't get session:" + err);
                    callback(null, "Can't get session");
                }
                else {
                    if (session.isValid()) {
                        callback(session.getIdToken().getJwtToken(), null);
                    } else {
                        console.log("CognitoUtil: Session is invalid");
                        callback(null, "Session is invalid")
                    }
                }
            });
        else
            console.log(" CognitoUtil: Can't get current user");
            callback(null, "Can't get current user");
    }

    getRefreshToken(callback): void {
        if (callback == null) {
            throw("CognitoUtil: callback in getRefreshToken is null...returning");
        }
        if (this.getCurrentUser() != null)
            this.getCurrentUser().getSession(function (err, session) {
                if (err) {
                    console.log("CognitoUtil: Can't get session:" + err);
                    callback(null, "Can't get session");
                }

                else {
                    if (session.isValid()) {
                        callback(session.getRefreshToken());
                    } else {
                        console.log("CognitoUtil: Session is invalid");
                        callback(null, "Session is invalid")
                    }
                }
            });
        else
            console.log(" CognitoUtil: Can't get current user");
            callback(null, "Can't get current user");
    }

    refresh(): void {
        this.getCurrentUser().getSession(function (err, session) {
            if (err) {
                console.log("CognitoUtil: Can't get session:" + err);
            }
            else {
                if (session.isValid()) {
                    console.log("CognitoUtil: refreshed successfully");
                } else {
                    console.log("CognitoUtil: refreshed but session is still not valid");
                }
            }
        });
    }
}
